<template>
  <div class="range-list">
    <h3>RangeList</h3>
    <P>One range calculator</P>
    <div class="range-box">
      <el-input
        class="input-item range-input"
        v-model="range_input"
        placeholder="Please input an one range calculator"
      >
      </el-input>
      <el-button
        type="primary"
        class="input-item"
        :icon="Plus"
        @click="add_range"
      />
      <el-button
        type="primary"
        class="input-item"
        :icon="Delete"
        @click="delete_range"
      />
    </div>
    <div class="current-range-list">
      <p>
        <span>the range :</span>
        <span v-if="range_list_str === ''">empty</span>
        <span class="current-str">{{ range_list_str }}</span>
      </p>
      <p>
        <span>the range list: </span><span>{{ range_list }}</span>
      </p>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive } from "vue";
import { ElMessage } from "element-plus";
import { Plus, Delete } from "@element-plus/icons-vue";
const range_input = ref("[1,5]");
const range_list_str = ref("");
const range_list = reactive([]);

function isInteger(item) {
  return typeof item === "number" && item % 1 === 0;
}

// 类型检查
function check_type(range_input_item) {
  let range_item = "";

  try {
    range_item = eval(range_input_item);
  } catch (err) {
    ElMessage({
      type: "warning",
      message: range_input_item + " is not an range",
    });
    return false;
  }

  //判断是否为数组
  if (Array.isArray(range_item) === false) {
    ElMessage({
      type: "warning",
      message: range_input_item + " is not an range",
    });
    return false;
  } else if (range_item.length != 2) {
    ElMessage({
      type: "warning",
      message:
        range_input_item +
        " is not an range , please input an range like [1,5]",
    });
    return false;
  } else {
    //判断是否为整数
    range_item.forEach(function (item) {
      if (isInteger(item) === false) {
        ElMessage({
          type: "warning",
          message: range_input_item + " is not an int in range",
        });
        return false;
      }
    });
  }

  //判断是否为有效范围
  if (range_item[0] > range_item[0]) {
    ElMessage({
      type: "warning",
      message: range_input_item + " is not an range",
    });
    return false;
  }

  return range_item;
}
//更新当前范围方法
function update_range_list(range_item, update_type) {
  let range_first = range_item[0];

  let range_last = range_item[1];

  const start_index = binary_search(range_list, range_first);

  const end_index = binary_search(range_list, range_last);

  const insert_nums = [];

  let splice_count = end_index - start_index;

  if (update_type === "add") {
    //添加范围
    if (start_index % 2 == 0) {
      insert_nums.push(range_first);
    }

    if (end_index % 2 == 0) {
      if (
        end_index < range_list.length &&
        range_last == range_list[end_index]
      ) {
        splice_count++;
      } else {
        insert_nums.push(range_last);
      }
    }
  } else {
    //删除范围
    if (start_index % 2 != 0) {
      insert_nums.push(start_index);
    }
    if (end_index % 2 != 0) {
      if (end_index == Math.abs(range_list[end_index])) {
        splice_count++;
      } else {
        insert_nums.push(end_index);
      }
    }
  }

  range_list.splice(start_index, splice_count, ...insert_nums);
}

/**
 * binary_search 二分查找方法
 * @param {索引} val
 * @return 如果找到就返回下标，如果没找到就返回-1
 */
function binary_search(range_list, val) {
  let low = 0,
    high = range_list.length - 1;
  while (low <= high) {
    const mid = Math.trunc((high + low) / 2);
    const curValue = Math.abs(range_list[mid]);
    switch (true) {
      case val < curValue:
        high = mid - 1;
        break;
      case val > curValue:
        low = mid + 1;
        break;
      case val == curValue:
        return mid;
    }
  }
  return low;
}

//新增范围
const add_range = () => {
  //类型检查
  let add_range_item = check_type(range_input.value);

  //更新当前范围
  update_range_list(add_range_item, "add");

  //输出当前范围列表
  print_range_list();

  //初始化
  range_input.value = "[]";
};

//删除范围
const delete_range = () => {
  //类型检查
  let delete_range_item = check_type(range_input.value);

  update_range_list(delete_range_item, "delete");

  //输出当前范围列表
  print_range_list();

  //初始化
  range_input.value = "[]";
};

//输出范围方法
const print_range_list = () => {
  let strs = "";

  let range_times = range_list.length / 2;

  for (let index = 0; index < range_times; index++) {
    strs +=
      "[" + range_list[2 * index] + "," + range_list[2 * index + 1] + ") ";
  }

  range_list_str.value = strs;
};
</script>

